import { Component, OnInit, OnDestroy } from '@angular/core';
import { Film } from 'src/app/services/interfaces/film';
import { DataFilmService } from 'src/app/services/data-film.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-films',
	templateUrl: './films.page.html',
	styleUrls: [ './films.page.scss' ]
})
export class FilmsPage implements OnInit, OnDestroy {
	public searchInput: string;
	public mesFilm: Film[];
	private filmSub: Subscription;

	constructor(private navCtrl: NavController, private filmService: DataFilmService) {
		this.mesFilm = [ ...filmService.myFilm ];
	}

	ngOnInit() {
		this.filmSub = this.filmService.myFilmChanged.subscribe((film) => {
			this.mesFilm = film;
		});
	}

	ngOnDestroy() {
		this.filmSub.unsubscribe();
	}

	goToAjouter() {
		this.navCtrl.navigateForward([ '/medias/films/ajouter' ]);
	}

	showInfo(id: number) {
		this.navCtrl.navigateForward([ '/info-film/' + id ]);
	}
}
