import { Component, OnInit, OnDestroy } from '@angular/core';
import { Film } from 'src/app/services/interfaces/film';
import { DataFilmService } from 'src/app/services/data-film.service';
import { AlertController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-films',
	templateUrl: './films.page.html',
	styleUrls: [ './films.page.scss' ]
})
export class FilmsPage implements OnInit, OnDestroy {
	public filmsRec: Film[];
	private filmSub: Subscription;

	constructor(private filmService: DataFilmService, private alertCtrl: AlertController) {
		this.filmsRec = [ ...this.filmService.recFilm ];
		this.filmsRec = this.shuffle(this.filmsRec);
	}

	ngOnInit() {
		this.filmSub = this.filmService.recFilmChanged.subscribe((film) => {
			this.filmsRec = film;
		});
	}

	ngOnDestroy() {
		this.filmSub.unsubscribe();
	}

	shuffle(array) {
		var currentIndex = array.length,
			temporaryValue,
			randomIndex;
		while (0 !== currentIndex) {
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex -= 1;
			temporaryValue = array[currentIndex];
			array[currentIndex] = array[randomIndex];
			array[randomIndex] = temporaryValue;
		}
		return array;
	}

	async supprimer(f: Film) {
		const alert = await this.alertCtrl.create({
			header: 'Suprimer un Film',
			message: 'Voulez vous vraiment supprimer ce film de vos recommandations ?',
			buttons: [
				'Annuler',
				{
					text: 'Valider',
					role: 'valider',
					handler: () => {
						var i: number = this.filmsRec.indexOf(f);
						this.filmsRec.splice(i, 1);
						this.filmService.delInRecFilm(f);
					}
				}
			]
		});

		await alert.present();
	}
}
